import { useRef, useState } from 'react';
import useAtlasStore from '../store/useAtlasStore';
import useMapInteraction from '../hooks/useMapInteraction';
import MapControls from '../components/map/MapControls';
import ImportForm from '../components/map/ImportForm';
import Toast from '../components/Toast';
import './MapView.css';

/**
 * 
 * 
 */
function MapView() {
  const mapContainer = useRef(null);
  const [importOpen, setImportOpen] = useState(false);
  
  const { 
    toast,
    showToast,
    exportToCSV,
    importFromCSV
  } = useAtlasStore();
  
  // 
  const { userLocation, flyToUserLocation } = useMapInteraction(mapContainer);
  
  // 
  const handleImportSubmit = (file) => {
    if (!file) {
      showToast('', 'warning');
      return;
    }
    
    importFromCSV(file);
    setImportOpen(false);
  };
  
  return (
    <div className="map-view">
      <div ref={mapContainer} className="map-container" />
      
      {/*  */}
      <MapControls
        onExportClick={exportToCSV}
        onImportClick={() => setImportOpen(true)}
        userLocation={userLocation}
        onLocationClick={flyToUserLocation}
      />
      
      {/*  */}
      <ImportForm
        isOpen={importOpen}
        onClose={() => setImportOpen(false)}
        onSubmit={handleImportSubmit}
      />
      
      {/*  */}
      <Toast 
        show={toast.show} 
        message={toast.message} 
        type={toast.type} 
      />
    </div>
  );
}

export default MapView;
