import { useEffect, useRef, useState } from 'react';
import maplibregl from 'maplibre-gl';
import useAtlasStore from '../store/useAtlasStore';

/**
 * 
 * @param {Object} containerRef - DOMref
 * @returns {Object} - 
 */
const useMapInteraction = (containerRef) => {
  const map = useRef(null);
  const popup = useRef(null);
  const [selectedFeature, setSelectedFeature] = useState(null);
  const [userLocation, setUserLocation] = useState(null);

  const { 
    visitedPlaces, 
    markPlaceAsVisited, 
    initializeStore 
  } = useAtlasStore();

  // 
  useEffect(() => {
    initializeStore();
    
    // 
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { longitude, latitude } = position.coords;
          setUserLocation([longitude, latitude]);
        },
        (error) => {
          console.log(':', error);
          // 
          setUserLocation([139.7528, 35.6852]);
        }
      );
    } else {
      // Geolocation API
      setUserLocation([139.7528, 35.6852]);
    }
  }, [initializeStore]);
  
  // 
  useEffect(() => {
    if (map.current || !containerRef.current) return;
    
    // 
    const initialCenter = userLocation || [139, 35];
    
    map.current = new maplibregl.Map({
      container: containerRef.current,
      style: {
        version: 8,
        sources: {
          'osm': {
            type: 'raster',
            tiles: [
              'https://a.tile.openstreetmap.org/{z}/{x}/{y}.png',
              'https://b.tile.openstreetmap.org/{z}/{x}/{y}.png',
              'https://c.tile.openstreetmap.org/{z}/{x}/{y}.png'
            ],
            tileSize: 256,
            attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          }
        },
        layers: [
          {
            id: 'osm-tiles',
            type: 'raster',
            source: 'osm',
            minzoom: 0,
            maxzoom: 19
          }
        ]
      },
      center: initialCenter,
      zoom: 3,
      touchZoomRotate: true,
      dragPan: true,
      doubleClickZoom: true,
      touchPitch: true
    });
    
    // 
    map.current.touchZoomRotate.enable();
    map.current.dragPan.enable();
    
    // 
    popup.current = new maplibregl.Popup({
      closeButton: false,
      closeOnClick: true
    });
    
    // 
    map.current.on('load', () => {
      // 
      map.current.addSource('countries', {
        type: 'geojson',
        data: 'https://raw.githubusercontent.com/datasets/geo-countries/master/data/countries.geojson'
      });
      
      // 
      map.current.addLayer({
        id: 'countries-fill',
        type: 'fill',
        source: 'countries',
        paint: {
          'fill-color': [
            'case',
            ['in', ['get', 'ISO_A2'], ['literal', visitedPlaces.map(p => p.countryCodeISO).filter(Boolean)]],
            '#ADD8E6',
            'rgba(0, 0, 0, 0)'
          ],
          'fill-opacity': 0.7
        }
      });
      
      // 
      map.current.addLayer({
        id: 'countries-outline',
        type: 'line',
        source: 'countries',
        paint: {
          'line-color': '#627BC1',
          'line-width': 1
        }
      });
      
      // 
      map.current.on('click', 'countries-fill', (e) => {
        if (!e.features || e.features.length === 0) return;
        
        const feature = e.features[0];
        const properties = feature.properties;
        
        // 
        const placeInfo = {
          uniqueId: properties.ISO_A2,
          placeName: properties.ADMIN,
          adminLevel: 'Country',
          countryCodeISO: properties.ISO_A2
        };
        
        setSelectedFeature(placeInfo);
        
        // 
        popup.current
          .setLngLat(e.lngLat)
          .setHTML(`
            <div class="map-popup">
              <h3>${properties.ADMIN}</h3>
              <button class="mark-visited-btn"></button>
            </div>
          `)
          .addTo(map.current);
        
        //  - DOM
        const markBtn = document.querySelector('.mark-visited-btn');
        if (markBtn) {
          markBtn.addEventListener('click', () => {
            markPlaceAsVisited(placeInfo);
            popup.current.remove();
          });
        }
      });
      
      // 
      map.current.on('mouseenter', 'countries-fill', () => {
        map.current.getCanvas().style.cursor = 'pointer';
      });
      
      map.current.on('mouseleave', 'countries-fill', () => {
        map.current.getCanvas().style.cursor = '';
      });
      
      // 
      if (userLocation) {
        map.current.flyTo({
          center: userLocation,
          zoom: 5,
          speed: 1.5,
          curve: 1.5
        });
        
        // 
        new maplibregl.Marker({
          color: '#e74c3c'
        })
          .setLngLat(userLocation)
          .addTo(map.current);
      }
    });
    
    return () => {
      if (map.current) {
        map.current.remove();
        map.current = null;
      }
    };
    
  }, [containerRef, visitedPlaces, markPlaceAsVisited, userLocation]);
  
  // 
  useEffect(() => {
    if (!map.current || !map.current.isStyleLoaded() || !map.current.getLayer('countries-fill')) return;
    
    try {
      map.current.setPaintProperty('countries-fill', 'fill-color', [
        'case',
        ['in', ['get', 'ISO_A2'], ['literal', visitedPlaces.map(p => p.countryCodeISO).filter(Boolean)]],
        '#ADD8E6',
        'rgba(0, 0, 0, 0)'
      ]);
    } catch (error) {
      console.error(':', error);
    }
    
  }, [visitedPlaces]);

  // 
  const flyToUserLocation = () => {
    if (!map.current || !userLocation) return;
    
    map.current.flyTo({
      center: userLocation,
      zoom: 5,
      speed: 1.5
    });
  };

  return {
    map,
    selectedFeature,
    userLocation,
    flyToUserLocation
  };
};

export default useMapInteraction;
