import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import useAtlasStore from '../store/useAtlasStore';
import Toast from '../components/Toast';
import './ListView.css';

function ListView() {
  const navigate = useNavigate();
  const { 
    visitedPlaces, 
    initializeStore, 
    toast, 
    removePlaceVisit,
    exportToCSV,
    importFromCSV
  } = useAtlasStore();
  
  const [sortBy, setSortBy] = useState('dateMarked');
  const [sortDirection, setSortDirection] = useState('desc');
  const [filter, setFilter] = useState('');
  const [adminFilter, setAdminFilter] = useState('all');
  const [displayData, setDisplayData] = useState([]);
  const [importOpen, setImportOpen] = useState(false);
  
  // 
  useEffect(() => {
    initializeStore();
  }, [initializeStore]);
  
  // 
  useEffect(() => {
    let filteredData = [...visitedPlaces];
    
    // 
    if (adminFilter !== 'all') {
      filteredData = filteredData.filter(place => place.adminLevel === adminFilter);
    }
    
    // 
    if (filter) {
      const lowerFilter = filter.toLowerCase();
      filteredData = filteredData.filter(place => 
        place.placeName.toLowerCase().includes(lowerFilter) ||
        place.uniqueId.toLowerCase().includes(lowerFilter)
      );
    }
    
    // 
    filteredData.sort((a, b) => {
      let valueA = a[sortBy];
      let valueB = b[sortBy];
      
      // 
      if (typeof valueA === 'string' && typeof valueB === 'string') {
        const result = valueA.localeCompare(valueB, 'ja');
        return sortDirection === 'asc' ? result : -result;
      }
      
      // 
      if (valueA < valueB) return sortDirection === 'asc' ? -1 : 1;
      if (valueA > valueB) return sortDirection === 'asc' ? 1 : -1;
      return 0;
    });
    
    setDisplayData(filteredData);
  }, [visitedPlaces, sortBy, sortDirection, filter, adminFilter]);
  
  // 
  const handleSortChange = (column) => {
    if (sortBy === column) {
      // 
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      // 
      setSortBy(column);
      setSortDirection('asc');
    }
  };
  
  // 
  const handleRowClick = (place) => {
    // TODO: URLID
    navigate('/');
  };
  
  // 
  const handleDelete = (e, uniqueId) => {
    e.stopPropagation(); // 
    if (window.confirm('')) {
      removePlaceVisit(uniqueId);
    }
  };
  
  // 
  const handleImportSubmit = (e) => {
    e.preventDefault();
    const file = e.target.csvFile.files[0];
    if (!file) {
      return;
    }
    
    importFromCSV(file);
    setImportOpen(false);
  };
  
  // 
  const formatDate = (isoDate) => {
    if (!isoDate) return '';
    const date = new Date(isoDate);
    return date.toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };
  
  return (
    <div className="list-view">
      <div className="list-controls">
        <div className="filter-controls">
          <input 
            type="text"
            className="search-input"
            placeholder="..."
            value={filter}
            onChange={(e) => setFilter(e.target.value)}
          />
          
          <select 
            className="admin-filter"
            value={adminFilter}
            onChange={(e) => setAdminFilter(e.target.value)}
          >
            <option value="all"></option>
            <option value="Country"></option>
            <option value="State"></option>
          </select>
        </div>
        
        <div className="action-buttons">
          <button 
            className="btn btn-sm"
            onClick={exportToCSV}
          >
            
          </button>
          <button 
            className="btn btn-sm"
            onClick={() => setImportOpen(true)}
          >
            
          </button>
        </div>
      </div>
      
      <div className="table-container">
        <table className="list-table">
          <thead>
            <tr>
              <th 
                className={sortBy === 'placeName' ? `sorted-${sortDirection}` : ''}
                onClick={() => handleSortChange('placeName')}
              >
                
              </th>
              <th 
                className={sortBy === 'adminLevel' ? `sorted-${sortDirection}` : ''}
                onClick={() => handleSortChange('adminLevel')}
              >
                
              </th>
              <th 
                className={sortBy === 'dateMarked' ? `sorted-${sortDirection}` : ''}
                onClick={() => handleSortChange('dateMarked')}
              >
                
              </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {displayData.length === 0 ? (
              <tr>
                <td colSpan="4" className="empty-message">
                  {filter || adminFilter !== 'all' 
                    ? '' 
                    : ''}
                </td>
              </tr>
            ) : (
              displayData.map((place) => (
                <tr 
                  key={place.uniqueId}
                  className="list-row"
                  onClick={() => handleRowClick(place)}
                >
                  <td>{place.placeName}</td>
                  <td>{place.adminLevel === 'Country' ? '' : ''}</td>
                  <td>{formatDate(place.dateMarked)}</td>
                  <td>
                    <button 
                      className="delete-btn"
                      onClick={(e) => handleDelete(e, place.uniqueId)}
                      title=""
                    >
                      
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
      
      {/*  */}
      <div className="list-counter">
        {displayData.length > 0 && (
          <p>
            {adminFilter === 'all' 
              ? `: ${displayData.length}` 
              : `${adminFilter === 'Country' ? '' : ''}: ${displayData.length}`}
          </p>
        )}
      </div>
      
      {/*  */}
      {importOpen && (
        <div className="import-overlay">
          <div className="import-container">
            <h3>CSV</h3>
            <form onSubmit={handleImportSubmit}>
              <div className="form-group">
                <label htmlFor="csvFile">CSV</label>
                <input 
                  type="file" 
                  id="csvFile" 
                  name="csvFile"
                  accept=".csv" 
                />
              </div>
              <div className="form-actions">
                <button 
                  type="button" 
                  className="btn btn-secondary"
                  onClick={() => setImportOpen(false)}
                >
                  
                </button>
                <button 
                  type="submit" 
                  className="btn btn-primary"
                >
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
      
      {/*  */}
      <Toast 
        show={toast.show} 
        message={toast.message} 
        type={toast.type} 
      />
    </div>
  );
}

export default ListView;
